import { fromPairs, map, compose, tail, head, toPairs, mergeAll, toUpper } from 'ramda';
import React from 'react';
import autoBind from 'auto-bind';
import powerpointReplace from 'powerpoint-replace';
import { XTNA } from '../xtna';
import { Benzinger } from '../benzinger';
import utils from '../../utils';

let FileSaver = null;

export default class HumanFactorsReportGenerator extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {};
  }
  componentDidMount() {
    if (FileSaver === null) {
      FileSaver = require('browser-filesaver');
    }
  }
  render() {
    return (
      <form style={{ marginBottom: '10px' }}>
        <input type='button' className='btn btn-success' onClick={this.export} value='Reporte Human Factors' disabled={!this.props.valid} />
      </form>
    );
  }
  export() {
    powerpointReplace(this.props.reportTemplateUrl, this.getReplacements(), 1)
    .then((blob) => FileSaver.saveAs(blob, `Reporte - ${this.props.personalData.name}.pptx`));
  }
  getReplacements() {
    const changeResultValues = ['Factor consistente', 'Factor de cambio moderado', 'Factor de cambio intenso'];
    const descriptions = ['alta consistencia', 'consistencia promedio alto', 'baja consistencia', 'muy baja consistencia']
    
    const xtna = fromPairs(map((prop) => [prop, parseInt(this.props.xtna[prop])], XTNA.props()));
    const benzinger = fromPairs(map((prop) => [prop, (this.props.benzinger[prop] < 10 ? ' ' : '') + this.props.benzinger[prop]], Benzinger.props()));
    const personal = this.props.personalData;
    const results = XTNA.getResults(xtna);

    const mapChangeToResult = (change) => changeResultValues[(change >= 2) + (change >= 4)];
    const changeResults = fromPairs(map(compose((change) =>
      [`${tail(head(change))}_CHANGE_RESULT`, mapChangeToResult(tail(change))]), toPairs(results.changes)));

    const dif = Math.abs(this.props.benzinger.L1 - this.props.benzinger.L2 * 4) + Math.abs(this.props.benzinger.A1 - this.props.benzinger.A2 * 4) + Math.abs(this.props.benzinger.I1 - this.props.benzinger.I2 * 4) + Math.abs(this.props.benzinger.V1 - this.props.benzinger.V2 * 4);
    const sum = this.props.benzinger.L2 * 4 + this.props.benzinger.A2 * 4 + this.props.benzinger.V2 * 4 + this.props.benzinger.I2 * 4;

    const benzingerResults = {
      changes: {
        'dFI 7': Math.abs(this.props.benzinger.A1 - this.props.benzinger.A2 * 4),
        'dPI 1': Math.abs(this.props.benzinger.L1 - this.props.benzinger.L2 * 4),
        'dPD 2': Math.abs(this.props.benzinger.I1 - this.props.benzinger.I2 * 4),
        'dFD 8': Math.abs(this.props.benzinger.V1 - this.props.benzinger.V2 * 4),
      },
      consistency: sum ? (1 - dif / sum) : null,
      change: dif
    };

    const fixSpacing = (num, spaces) => {
      if (num < 100) {
        return ' ' + (spaces !== 1 ? ' ' : '') + num;
      }

      return num;
    };

    const ch = utils.formatPercentage(benzingerResults.consistency);
    const textReplacements = mergeAll([{
        name: personal.name,
        company: personal.company,
        date: personal.date.format('DD/MM/YYYY'),
        age: personal.age + ' años',
        NPROFILE: results.NProfile.join(' - '),
        APROFILE: results.AProfile.join(' - '),
        CHANGE: results.change,
        CONSISTENCY: (results.consistency !== 1 ? ' ' : '') + utils.formatPercentage(results.consistency),
        AFI: fixSpacing(this.props.benzinger.A1),
        API: fixSpacing(this.props.benzinger.L1),
        APD: fixSpacing(this.props.benzinger.I1),
        AFD: fixSpacing(this.props.benzinger.V1),
        JFI: fixSpacing(this.props.benzinger.A2 * 4),
        JPI: fixSpacing(this.props.benzinger.L2 * 4),
        JPD: fixSpacing(this.props.benzinger.I2 * 4),
        JFD: fixSpacing(this.props.benzinger.V2 * 4),
        AEX: this.props.e1,
        AIN: this.props.i1,
        JEX: this.props.e2,
        JIN: this.props.i2,
        TEX: fixSpacing(this.props.e1 + this.props.e2, 1),
        TIN: fixSpacing(this.props.i1 + this.props.i2, 1),
        ARAC: fixSpacing(this.props.benzinger.A1 + this.props.benzinger.L1),
        ACRE: fixSpacing(this.props.benzinger.V1 + this.props.benzinger.I1),
        ACON: fixSpacing(this.props.benzinger.A1 + this.props.benzinger.V1),
        APRAC: fixSpacing(this.props.benzinger.L1 + this.props.benzinger.I1),
        JRAC: fixSpacing(this.props.benzinger.A2 * 4 + this.props.benzinger.L2 * 4),
        JCRE: fixSpacing(this.props.benzinger.V2 * 4 + this.props.benzinger.I2 * 4),
        JCON: fixSpacing(this.props.benzinger.A2 * 4 + this.props.benzinger.V2 * 4),
        JPRAC: fixSpacing(this.props.benzinger.L2 * 4 + this.props.benzinger.I2 * 4),
        BCONSISTENCY: benzingerResults.consistency ? (ch.length !== 4 ? ' ' + ch : ch) : '    -',
        BCHANGE: fixSpacing(benzingerResults.change)
      },
      xtna,
      benzinger,
      fromPairs(map(map(compose(toUpper, (x) => x.toString())), toPairs(results.derivedProperties))),
      fromPairs(toPairs(results.changes).map((change) => [toUpper(head(change)), tail(change)])),
      changeResults
    ]);

    const index = (results.consistency < .75) + (results.consistency < .60) + (results.consistency < .40);
    textReplacements.CONSISTENCY_RESULT = `${utils.formatPercentage(results.consistency)} ${descriptions[index]}`

    const canvas1Array = $('canvas#' + this.props.chartsIds.xtna);
    if (!canvas1Array.length) {
      throw new Error('Canvas with id: ' + this.props.chartsIds.xtna + ' id not mounted.');
    }

    const canvas2Array = $('canvas#' + this.props.chartsIds.benzinger);
    if (!canvas2Array.length) {
      throw new Error('Canvas with id: ' + this.props.chartsIds.benzinger + ' id not mounted.');
    }

    return {
      text: textReplacements,
      images: {
        'image7.png': head(canvas1Array).toDataURL().split(',')[1],
        'image8.png': head(canvas2Array).toDataURL().split(',')[1]
      }
    };
  }
}




