import React from 'react';
import autoBind from 'auto-bind';
import { validations } from 'shared';
import Spinner from '../utils/Spinner.jsx';
import Login from './auth/Login.jsx';
import './auth/TokenManager';
import Navbar from './Navbar.jsx';
import ReportingTool from './reports/ReportingTool.jsx';
import Users from './users/Users.jsx';
import Administration from './administration/Administration.jsx';
import Questionnaires from './questionnaires/Questionnaires.jsx'
import UserDetails from './users/UserDetails.jsx';

let apiLocation = '{{API_LOCATION}}:3000';
if (apiLocation[0] == '{') {
  apiLocation = 'http://localhost:3000';
}

export default class App extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {
      api: null,
      route: window.location.hash.substr(1),
      questionnaire: null
    };

    this.getApi();
    require('moment').locale('es');
  }
  componentDidMount() {
    window.addEventListener('hashchange', () => {
      this.setState({ route: window.location.hash.substr(1) })
    });
  }
  getApi() {
    $.ajax(apiLocation)
    .success((api) => this.setState({ api, loggingOut: false }))
    .fail(() => this.setState({ errorMessage: 'Hubo un error al cargar la aplicación.' }));
  }
  render() {
    if (this.state.loggingOut) {
      return <div>Saliendo...</div>;
    }

    if (this.state.errorMessage) {
      return <div>{this.state.errorMessage}</div>;
    }

    if (!this.state.api) {
      return <center><Spinner/></center>;
    }

    const routeParts = this.state.route.split('/');
    if (this.state.api._rels.questionnaire && routeParts.length > 1 && routeParts[1] === 'cuestionarios') {
      return <Questionnaires resourceUrl={this.state.api._rels.questionnaire.replace('{id}', routeParts[2])} />;
    }

    if (!this.state.api.user) {
      if (this.state.api._rels.login) {
        return <Login url={this.state.api._rels.login} onLogin={this.getApi}/>;
      }
      return <div>No se puede entrar por el momento.</div>
    }

    if (this.state.api.user) {
      return (
        <div>
          <Navbar user={this.state.api.user} onLogout={this.handleLogout} route={this.state.route} />
          <div className='container'>
            {this.getComponent(this.state.route)}
          </div>
        </div>
      );
    }
  }
  handleLogout() {
    window.location.hash = '/';
    this.setState({ api: null, loggingOut: true });
    this.getApi()
  }
  getComponent() {
    const routes = {
      reports: '/reportes',
      administration: '/administracion',
      users: '/usuarios',
      user: '/usuarios/:id'
    };

    const routeHandlers = {
      reports: () => <ReportingTool xtnaReportTemplate={this.state.api._rels.xtnaReportTemplate} humanFactorsReportTemplate={this.state.api._rels.humanFactorsReportTemplate}/>,
      administration: () => <Administration />,
      users: () => <Users resourceUrl={this.state.api._rels.users} />,
      user: ({id}) => <UserDetails resourceUrl={this.state.api._rels.getUserById.replace('{id}', id)} />
    };

    const matchingRoute = this.getMatchingRoute(this.state.route, routes);
    
    if (!matchingRoute.route) {
      window.location.hash = '/reportes';
      return routeHandlers.reports();
    }

    return routeHandlers[matchingRoute.route](matchingRoute.params);
  }
  getMatchingRoute(route, routes) {
    const forEachIndexed = R.addIndex(R.forEach);
    const currentRouteParts = route.split('/');

    let matchingRoute = null, routeParams = null;
    forEachIndexed((routeName, index) => {
      let parts = routes[routeName].split('/');
      
      if (matchingRoute || parts.length !== currentRouteParts.length) {
        return;
      }

      const params = {};
      let matching = true;
      forEachIndexed((part, partIndex) => {
        if (!matching) {
          return;
        }

        const currentRoutePart = currentRouteParts[partIndex];
        if (part[0] === ':') {
          params[R.tail(part)] = currentRoutePart;
        } else {
          matching = currentRoutePart === part;
        }
      }, parts);

      if (matching) {
        matchingRoute = routeName;
        routeParams = params;
      }
    }, Object.keys(routes));

    return {
      route: matchingRoute,
      params: routeParams
    }
  }
}



