import React from 'react';
import autoBind from 'auto-bind';
import TokenManager from './auth/TokenManager';
import logo from './assets/logo-no-text.png';

export default class Navbar extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {};
  }
  handleLogout() {
    TokenManager.clearToken();
    this.props.onLogout();
  }
  render() {
    const tabs = [
      { url: '#/reportes', label: 'Reportes' },
      { url: '#/usuarios', label: 'Usuarios' },
      // { url: '#/administracion', label: 'Administración' }
    ];

    return (
      <nav className='navbar navbar-default' style={{ marginBottom: '30px' }}>
        <div className='container'>
          <div className='navbar-header'>
            <button onClick={() => this.forceUpdate()} type='button' className='navbar-toggle collapsed' data-toggle='collapse' data-target='#navbar' aria-expanded='false' aria-controls='navbar'>
              <span className='sr-only'>Toggle navigation</span>
              <span className='icon-bar'></span>
              <span className='icon-bar'></span>
              <span className='icon-bar'></span>
            </button>
            <a style={{ padding: '7px 20px' }} className='navbar-brand' href='#'>
              <img src={logo} height='36px' />
            </a>
          </div>
          <div id='navbar' className='navbar-collapse collapse' aria-expanded='false'>
            <ul className='nav navbar-nav'>
              {tabs.map(this.renderTab)}
            </ul>
            <ul className='nav navbar-nav navbar-right'>
              <li><a>{this.props.user.username}</a></li>
              <li><a href='#' onClick={this.handleLogout}>Salir</a></li>
            </ul>
          </div>
        </div>
      </nav>
    );
  }
  renderTab(tab) {
    const isActive = this.props.route.startsWith(tab.url.substring(1));
    return <li key={tab.label} className={isActive ? 'active' : ''}><a href={tab.url}>{tab.label}</a></li>;
  }
}



