import { clone, fromPairs, compose, reverse, append, __ } from 'ramda';

const props = ['L1', 'A1', 'V1', 'I1', 'L2', 'A2', 'V2', 'I2'];

export default {
  props: () => clone(props),
  default: () => ({
    L1: 0,
    A1: 0,
    V1: 0,
    I1: 0,
    L2: 0,
    A2: 0,
    V2: 0,
    I2: 0
  })
};


