import { merge } from 'ramda';
import React from 'react';
import autoBind from 'auto-bind';
import NumberInput from 'number-input';
import Chart from './Chart.jsx'
import '../app.scss';
import Benzinger from './Benzinger';

export default class BenzingerChartDataForm extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {};
  }
  render() {
    return (
      <div>
        <table className='input-table' style={{ marginBottom: '10px' }}>
          <thead>
            <tr>
              {['', 'FI 7', 'PI 1', 'PD 2', 'FD 8'].map((prop) => <td key={prop}>{prop}</td>)}
            </tr>
          </thead>
          <tbody>
            <tr><td><b>Adulto</b></td>{['A1', 'L1', 'I1', 'V1'].map(this.renderProp(0))}</tr>
            <tr><td><b>Joven</b></td>{['A2', 'L2', 'I2', 'V2'].map(this.renderProp(1))}</tr>
            <tr style={{ color: 'blue' }}>
              <td><b>Joven x 4</b></td>
              <td>{this.props.value.A2 * 4}</td>
              <td>{this.props.value.L2 * 4}</td>
              <td>{this.props.value.I2 * 4}</td>
              <td>{this.props.value.V2 * 4}</td>
            </tr>
          </tbody>
        </table>
        <table className='input-table' style={{ marginBottom: '10px' }}>
          <thead>
            <tr>
              {['', 'Extr', 'Intr'].map((prop) => <td key={prop}>{prop}</td>)}
            </tr>
          </thead>
          <tbody>
            <tr>
              <td key='rowAdulto'><b>Adulto</b></td>
              <td key='extrAdulto'><NumberInput className='form-control' onChange={this.handleStatChange('e1')} value={this.props.stats.e1} min='0' max='6'></NumberInput></td>
              <td key='intrAdulto'><NumberInput className='form-control' onChange={this.handleStatChange('i1')} value={this.props.stats.i1} min='0' max='6'></NumberInput></td>
            </tr>
            <tr>
              <td key='rowJoven'><b>Joven</b></td>
              <td key='extrJoven'><NumberInput className='form-control' onChange={this.handleStatChange('e2')} value={this.props.stats.e2} min='0' max='6'></NumberInput></td>
              <td key='intrJoven'><NumberInput className='form-control' onChange={this.handleStatChange('i2')} value={this.props.stats.i2} min='0' max='6'></NumberInput></td>
            </tr>
            <tr style={{ color: 'black' }}>
              <td key='rowTotal'><b>Total</b></td>
              <td key='extrJoven'><b>{this.props.stats.e1 + this.props.stats.e2}</b></td>
              <td key='intrJoven'><b>{this.props.stats.i1 + this.props.stats.i2}</b></td>
            </tr>
          </tbody>
        </table>
      </div>
    );
  }
  handleStatChange(id) {
    return (value) => {
      this.props.onStatChange(merge(this.props.stats, { [id]: value }));
    }
  }
  renderProp(type) {
    return (prop) => {
      const cell = this.props.editable === false ?
        this.props.value[prop] :
        <NumberInput className='form-control' onChange={this.handleChange(prop)} value={this.props.value[prop]} min='0' max={[150, 59][type]}></NumberInput>
      return <td key={prop}>{cell}</td>;
    }
  }
  handleChange(field) {
    return (value) => {
      this.props.onChange(merge(this.props.value, { [field]: value, valid: true }));
    };
  }
}


