import { merge, bind } from 'ramda';
import moment from 'moment';
import React from 'react';
import autoBind from 'auto-bind';
import { XTNA, XTNAChart, XTNAForm, XTNAResults } from '../xtna';
import { Benzinger, BenzingerChart, BenzingerForm, BenzingerResults } from '../benzinger';
import ExcelDataExtractor from './ExcelDataExtractor.jsx';
import XTNAReportGenerator from './XTNAReportGenerator.jsx';
import HumanFactorsReportGenerator from './HumanFactorsReportGenerator.jsx';
import PersonalDataForm from './PersonalDataForm.jsx';

export default class ReportingTool extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {
      xtna: merge({ valid: true }, XTNA.default()),
      benzinger: merge({ valid: true }, Benzinger.default()),
      benzingerStats: { i1: 0, i2: 0, e1: 0, e2: 0 },
      personal: { name: '', company: '', age: 0, date: moment(), valid: false }
    };
  }
  render() {
    const canGenerateXTNAReport = this.state.xtna.valid && this.state.personal.valid && this.props.xtnaReportTemplate;
    const canGenerateHumanFactorsReport = this.state.benzinger.valid && this.state.personal.valid && this.props.humanFactorsReportTemplate;

    return (
      <div>
        <div className='col-xs-10 col-xs-offset-1 col-md-offset-0 col-md-4' style={{ margin: '20px 0px' }}>
          <PersonalDataForm value={this.state.personal} onChange={(value) => this.setState({ personal: value })} />
          <ExcelDataExtractor onChange={bind(this.setState, this)}/>
          <XTNAReportGenerator personalData={this.state.personal} xtna={this.state.xtna} valid={canGenerateXTNAReport} chartId='xtna-chart' reportTemplateUrl={this.props.xtnaReportTemplate}/>
          <HumanFactorsReportGenerator personalData={this.state.personal} xtna={this.state.xtna} benzinger={this.state.benzinger} valid={canGenerateHumanFactorsReport} chartsIds={{ xtna: 'xtna-chart', benzinger: 'benzinger-chart' }} reportTemplateUrl={this.props.humanFactorsReportTemplate} {...this.state.benzingerStats}/>
        </div>
        <div className='col-xs-10 col-xs-offset-1 col-md-offset-0 col-md-4' style={{ margin: '20px 0px' }}>
            <XTNAChart {...this.state.xtna} id='xtna-chart'/>
          <center>
            <XTNAForm value={this.state.xtna} onChange={(value) => this.setState({ xtna: value })} />
          </center>
          <XTNAResults {...this.state.xtna}/>
        </div>
        <div className='col-xs-10 col-xs-offset-1 col-md-offset-0 col-md-4' style={{ margin: '20px 0px' }}>
          <BenzingerChart {...this.state.benzinger} id='benzinger-chart'/>
          <center>
            <BenzingerForm value={this.state.benzinger} stats={this.state.benzingerStats} onStatChange={(stats) => this.setState({ benzingerStats: stats })} onChange={(value) => this.setState({ benzinger: value })}/>
          </center>
          <BenzingerResults {...this.state.benzinger} />
        </div>
      </div>
    );
  }
}



