import R from 'ramda';
import $ from 'jquery';
import React from 'react';
import Promise from 'bluebird';
import Modal from 'react-bootstrap/lib/Modal';
import Alert from 'react-bootstrap/lib/Alert';
import autoBind from 'auto-bind';
import { validations } from 'shared';
import Spinner from '../../utils/Spinner.jsx';
import Table from './Table.jsx';
import CreateUserForm from './CreateUserForm.jsx';
import UserDetails from './UserDetails.jsx';
import Pagination from 'react-bootstrap/lib/Pagination';
import FilterControls from './FilterControls.jsx';

const pageSize = 20;
const columns = [
  { id: 'id', label: 'ID', maxWidth: 1 },
  { id: 'name', label: 'Nombre', maxWidth: 4 },
  { id: 'company', label: 'Company', maxWidth: 4 },
  { id: 'age', label: 'Edad', maxWidth: 1 },
  { id: 'sex', label: 'Sexo', maxWidth: 1 },
  { id: 'expiration', label: 'Fecha de Expiración', maxWidth: 2 },
  { id: 'status', label: 'Estatus Cuestionarios', maxWidth: 2 },
  { id: 'completionDate', label: 'Fecha de Respuesta', maxWidth: 2 },
  { id: 'email', label: 'Email', maxWidth: 4 }
];

export default class Users extends React.Component {
  constructor(props) {
    super(props);
    autoBind(this);
    this.state = {
      creatingUser: false,
      filters: {
        name: '',
        company: '',
        email: '',
        sex: ''
      },
      sortColumn: 'id',
      sortOrder: 'asc',
      showingFilters: false,
      selectedRow: null,
      page: 0,
      usersResource: null
    };
  }
  componentDidMount() {
    this.fetchUsers(this.props.resourceUrl);
  }
  static getQuestionnaireStatus(stage) {
    return R.prop(stage, ['No iniciado', 'Datos Personales', 'Cuestionario 1', 'Cuestionario 2', 'Completado']);
  }
  render() {
    if (!this.state.usersResource && this.state.loadingUsers) {
      return <Spinner />;
    }

    if (!this.state.usersResource && !this.state.loadingUsers) {
      return <div>{this.state.errorMessage}</div>;
    }
    
    const resource = this.state.usersResource;
    const numOfPages = Math.ceil(resource.total / pageSize);
    const usersIndices = R.range(this.state.page * pageSize, (this.state.page + 1) * pageSize);
    const selectedIndex = this.state.selectedRow ? this.state.selectedRow.index : null;
    const data = this.state.loadingUsers ? [] : usersIndices
    .map(R.compose(R.prop(R.__, resource._embedded)))
    .filter(R.identity)
    .map(({ id, name, company, age, sex, questionnaire, email }) => 
      ({ id, name, company, age, sex, email,
        expiration: questionnaire.expirationDate ? moment(questionnaire.expirationDate).format('DD/MM/YYYY') : '-',
        completionDate: questionnaire.completionDate ? moment(questionnaire.completionDate).format('DD/MM/YYYY') : '-',
        status: Users.getQuestionnaireStatus(questionnaire.stage)
      }));

    return (
      <div style={this.state.loadingUsers ? { cursor: 'progress' } : {}}>
        <button type='button' className='btn btn-success' onClick={this.handleOpenCreateUserModal}>Crear Usuario</button>
        <button className='btn btn-default' style={{ margin: '10px' }} onClick={() => this.setState({ showingFilters: !this.state.showingFilters })}>Filtrar Usuarios</button>
        <If condition={this.state.selectedRow !== null}>
          <button type='button' className='btn btn-info'  style={{ marginRight: '10px' }} onClick={() => window.location.hash = `/usuarios/${this.state.selectedRow.row.id}`}>Ver Detalles</button>
        </If>
        <button type='button' className='btn btn-danger' onClick={this.exportUsers}>Exportar Usuarios</button>
        <If condition={this.state.newUser} >
          {this.renderCreateUserModal()}
        </If>
        <If condition={this.state.showingFilters}>
          <FilterControls onSubmit={this.handleFiltersChange}/>
        </If>
        <Table columns={columns} sort={{ column: this.state.sortColumn, order: this.state.sortOrder }} data={data} selectedIndex={selectedIndex} onSort={this.handleSortChange} onRowClick={(event) => this.setState({ selectedRow: event })} onRowDoubleClick={({row}) => window.location.hash = `/usuarios/${row.id}`} />
        <If condition={this.state.loadingUsers} >
          <Spinner />
        </If>
        <If condition={resource}>
          <div>Mostrando resultados <b>{Math.min(usersIndices[0] + 1, resource.total)}</b>-<b>{Math.min(usersIndices[usersIndices.length - 1] + 1, resource.total)}</b> de <b>{resource.total}</b> resultado{resource.total !== 1 ? 's' : ''}</div>
        </If>
        <If condition={resource.total > pageSize}>
          <center>
            <Pagination prev={this.state.page !== 0} next={(this.state.page + 1) * pageSize <= resource.total} first={this.state.page !== 0} last={(this.state.page + 1) * pageSize <= resource.total} bsSize="medium" maxButtons={10} items={Math.ceil(resource.total / pageSize)} activePage={this.state.page + 1} onSelect={this.handlePageChange} />
          </center>
        </If>
      </div>
    );
  }
  exportUsers() {
    Promise.resolve($.ajax({ url: this.state.usersResource._rels.export }))
    .then((x) => window.location = (encodeURI('data:text/csv;charset=utf-8,' + x)))
    .catch(() => alert('Ocurrió un error al exportar los usuarios.'));
  }
  renderCreateUserModal() {
    const closeModal = () => this.setState({ newUser: null, modalErrorMessage: null })
    const userValidations = R.pick(['email'], validations);
    const validUserForm = () => R.is(Object, this.state.newUser) && validations.validateObject(userValidations, this.state.newUser)
    return (
      <Modal show={true} onHide={closeModal}>
        <Modal.Header closeButton>
          <Modal.Title>Crear Usuario</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <If condition={this.state.modalErrorMessage} >
            <Alert bsStyle="danger" onDismiss={() => this.setState({ modalErrorMessage: null })}>{this.state.modalErrorMessage}</Alert>
          </If>
          <CreateUserForm value={this.state.newUser} onChange={(newUser) => this.setState({ newUser })} />
        </Modal.Body>
        <Modal.Footer>
          <button type='button' className='btn btn-danger' onClick={closeModal}>Cancelar</button>
          <button type='submit' className='btn btn-success' onClick={this.handleCreateUser} disabled={!validUserForm() || this.state.creatingUser}>Crear</button>
        </Modal.Footer>
      </Modal>
    );
  }
  handleSortChange({ column, order }) {
    const prevColumn = this.state.sortColumn, prevOrder = this.state.sortOrder;
    this.setState({ sortColumn: column, sortOrder: order });
    const url = this.state.usersResource._rels.sort.replace('{sort}', column === 'status' ? 'stage' : column).replace('{order}', order);
    this.fetchUsers(url, this.state.page)
    .catch(() => this.setState({ sortColumn: prevColumn, sortOrder: prevOrder }))
  }
  handleFiltersChange(filters) {
    const url = this.state.usersResource._rels.search
      .replace('{name}', filters.name || '')
      .replace('{company}', filters.company || '')
      .replace('{sex}', filters.sex || '')
      .replace('{email}', filters.email || '')
      .replace('{stage}', filters.stage || '');
    this.fetchUsers(url);
  }
  handlePageChange(event, selectedEvent) {
    const page = selectedEvent.eventKey - 1;
    this.fetchUsers(this.state.usersResource._rels.getPage.replace('{pageNum}', page), page);
  }
  fetchUsers(url, page = 0) {
    if (this.state.loadingUsers) {
      return Promise.reject('Already fetching users.');
    }
    const prevPage = this.state.page;
    this.setState({ loadingUsers: true, selectedRow: null, page });
    return Promise.resolve($.ajax({ url }))
    .then((usersResource) => this.setState({ usersResource }))
    .catch(() => this.setState({ page: prevPage, errorMessage: 'Hubo un error al cargar los usuarios.' }))
    .finally(() => this.setState({ loadingUsers: false }));
  }
  handleOpenCreateUserModal() {
    this.setState({ newUser: { name: '', email: '', expirationDays: 7 } });
  }
  handleCreateUser(event) {
    event.preventDefault();
    this.setState({ creatingUser: true });
    return Promise.resolve($.ajax({
      url: this.props.resourceUrl,
      method: 'POST',
      data: this.state.newUser
    })).then(() => {
      this.setState({ newUser: null, modalErrorMessage: null });
      this.fetchUsers(this.state.usersResource._rels.self, this.state.page);
    }).catch(() => this.setState({ modalErrorMessage: 'Hubo un error al crear el usuario.' }))
    .finally(() => this.setState({ creatingUser: false }));
  }
}


